import React, { useEffect } from 'react';

class WidgetEmbedCode extends React.Component {
  componentDidMount() {
    this.injectScript();
  }

  UNSAFE_componentWillReceiveProps({ portalId: nextPortalId, env: nextEnv }) {
    if (nextPortalId !== this.props.portalId || nextEnv !== this.props.env) {
      if (document.getElementById('hs-script-loader')) {
        // document.getElementById('hs-script-loader').remove();

        debugger;
        this.injectScript();
      }
    }
  }

  injectScript() {
    const { portalId = 99209436, env = 'qa' } = this.props;

    const s = document.createElement('script');

    s.type = 'text/javascript';
    s.id = 'hs-script-loader';
    s.async = true;
    s.defer = true;
    s.src = `//js.hs-scripts${env === 'qa' ? 'qa' : ''}.com/${portalId}.js`;

    document.body.appendChild(s);
  }

  render() {
    return <div />;
  }
}

export default WidgetEmbedCode;
